body {
  margin: 0;
}

ul, ol, li {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

*, :before, :after {
  border: 0;
  margin: 0;
  padding: 0;
}

.header {
  --main-blue-color: #00acdc;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo, .header__menu, .header__list {
  display: flex;
}

.header__item {
  transition: color .3s;
  position: relative;
}

.header__link {
  text-transform: uppercase;
  flex-wrap: wrap;
  align-content: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14.6px;
  display: flex;
}

.header__item:not(:last-child) {
  margin-right: 20px;
}

.is-active {
  color: var(--main-blue-color);
}

.is-active:after {
  content: "";
  background-color: var(--main-blue-color);
  cursor: default;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.header__item:hover {
  color: var(--main-blue-color);
}

